body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }

.app_header-title {
  text-align: center; }

.app_main {
  margin: 0;
  width: 100%; }

.input_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 25px;
  width: 800px; }

.pokemon_filtered-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  list-style: none; }

.app_field-input {
  width: 200px; }

.pokemon_img-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }

.pokemon_name {
  margin-bottom: 5px;
  margin-top: 10px;
  text-transform: uppercase;
  color: black;
  text-align: center; }

.pokemon_card {
  background-color: #e0e0f5;
  width: 200px;
  height: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 15px;
  border-radius: 50%;
  transition: color ease 1s, border-color ease 1s; }
  .pokemon_card:hover {
    color: red;
    background-color: white;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25); }

.list_item-link {
  text-decoration: none; }

.link_return {
  text-decoration: none;
  color: red;
  margin-top: 20px;
  text-transform: uppercase; }

.app_main-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.pokemon_type {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0px;
  width: 100px;
  padding-right: 30px;
  padding-left: 30px;
  margin-left: 15px; }

.pokemon_type-list {
  background-color: black;
  color: white;
  border-radius: 40%;
  width: 65px;
  height: 20px;
  text-align: center;
  padding: 0px; }

.pokemon_evolutions {
  color: gray;
  font-size: 10px;
  text-align: center;
  margin-top: 0px; }

.pokemon_card-firstcontainer {
  background-color: red;
  width: 200px;
  height: 100px;
  width: 200px;
  border-radius: 50% / 100% 100% 0 0; }

.style-pokeball {
  background-color: black;
  height: 20px;
  width: 200px; }

.pokemon_id {
  background-color: white;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  color: black;
  border-color: black;
  text-align: center;
  margin: auto;
  margin-bottom: 15px; }

